.hamStick{
    width: 30px;
    height:2px;
    background: #fff;
}
.ham{
    height: 40px;    
    padding: 5px;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    transition: 0.2s;
}

.active .ham1{
    transform: rotate(45deg) translate(5px, 5px);
}
.active .ham1{
    transform: rotate(45deg) translate(5px, 5px);
}
.active .ham2{
    filter: opacity(0%);
}
.active .ham3{
    transform: rotate(-45deg)  translate(5px, -5px);
}
.ham1{
    
    transition: 0.2s;
}
.ham2{
    transition: 0.2s;
}
.ham3{
    
    transition: 0.2s;
}
.active{
    border: 1px solid grey;
    border-radius: 50%;
}

.icon{
    
    background-size: cover;
}
.resNav{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 70vh;
    right: 10%;
    z-index: 9;
    background-color: rgba(65, 65, 65, 0.356);
    backdrop-filter: blur(1px);
    padding: 10px;
    animation: height-in-vertical ease-in 0.4s;
}

.resNav .resTabs{
    padding: 8px 19px;
    border-radius: 10px;
    z-index: 10;
    background-color: rgba(85, 86, 87, 0.466);
    cursor: pointer;
    visibility: hidden;
    animation: slit-in-vertical ease-in 0.3s;
    animation-fill-mode: forwards;
    
}



.resNav .resTabs:nth-of-type(1){
    animation-delay: 0.1s;
}
.resNav .resTabs:nth-of-type(2){
    animation-delay: 0.15s;

}
.resNav .resTabs:nth-of-type(3){
    animation-delay: 0.2s;

}
.resNav .resTabs:nth-of-type(4){
    animation-delay: 0.25s;
}
.resNav .resTabs:nth-of-type(5){
    animation-delay: 0.3s;
}
@keyframes slit-in-vertical {
    0% {
      transform: translateY(-55px);
      opacity: 0;
    }
    100% {
      transform: translate(0);
      opacity: 1;
      visibility: visible;
    }
  
  }

  @keyframes height-in-vertical {
    0%{
        height: 0vh;
    }
    21%{
        height: 10vh;
    }
    32%{
        height: 20vh;
    }
    43%{
        height: 30vh;
    }
    54%{
        height: 40vh;
    }
    65%{
        height: 50vh;
    }
    76%{
        height: 60vh;
    }
    87%{
        height: 65vh;
    }
    98%{
        height: 70vh;
    }
    100%{

    }
  
  }
