.bgTester{
  background:radial-gradient(circle farthest-side at bottom right, #220040, #29023f, #37034e, #6c0570)
}
.shadowOnHover:hover{
  box-shadow: 5px 5px 10px 2px #000000;
}


/* green 3rd level */
@media only screen and (min-width:250px) and (max-width:750px) {
  .headSlider {
    margin-bottom: 45px;
    font-size: 2rem;
    margin-left: -55px;
    width: 750px;
    height: 75px;
  }
  
}
.iconName{
  position: absolute;
  top:0;
  left:0;
  width:100%;
  height: 100%;
  background-color: rgba(252, 252, 252, 0.548);
  backdrop-filter: blur(2px);
  justify-content: center;
  align-items: center;
  transition: 1s;
  animation: normal slit-in-vertical 0.3s ease;
}
.iconExit{
  animation: normal slit-out-vertical 0.3s ease;
}
.icontext{
  font-size: 18px;
  color: rgb(76, 1, 95);
  font-weight:1000;
  text-shadow: 1px 1px 1px rgb(76, 1, 95);
}

@keyframes slit-out-vertical {
  0% {
    transform: translate(0);
    opacity: 1;
  }
  100% {
    transform: translateY(20px);
    opacity: 0;
  }
  
}

.resume .button, .about .button{
   border: 1px solid;
   padding: 7px 14px;
   border-radius: 10px;

}

.resume .button:hover, .about .button:hover{
  background-color: #00a2ff52;
}

.resume .button:active, .about .button:active{
  background-color: #f5f5f552;
}

.onHoverBlack{
  padding: 14px 14px;
  display: inline-block;
  border-radius: 15px;
  transition: 0.3s;
  margin: 20px;
}
.onHoverBlack:hover{
  background-color: rgba(131, 131, 131, 0.562);
  color: #000;
}
.onHoverBlack:active{
  background-color: rgba(255, 255, 255, 0.301);
}

@media screen and (min-width:200px) and (max-width:500px) {
  .onHoverBlack{
    padding: 9px 9px;
    border-radius: 7px;
    margin: 7px;
  }
}
